import { Component, Prop, Vue } from "vue-property-decorator";

import { firestore, storage, auth } from '@/firebaseInit';
import { ParentCategoryInfo } from '@/dto/ParentCategoryInfo';
import { AuditFields } from '@/dto/AuditFields';
import { FirestoreService } from '@/services/FirestoreService';
import { UtilityService } from '@/services/UtilityService';

@Component
export default class ParentCategory extends Vue {
  @Prop({ required: false, default: function () { return new ParentCategoryInfo() } })
  parentCategoryInfo!: ParentCategoryInfo;
  @Prop({ required: false, default: false })
  edit!: boolean;

  selectedImageUrl = "";
  selectedImage: any = null;
  loading = false;
  label = "Add Main Category";
  uploadRules = [
    (value: any) => !!value || 'logo is required',
    (value: any) =>
      !value ||
      value.size < 100000 ||
      'logo must be less than 100kb',
  ]
  async mounted() {
    this.label = this.edit ? "Edit Main Category" : "Add Main Category";
    if (this.edit) {
      this.selectedImageUrl = this.parentCategoryInfo.logoUrl;
      this.uploadRules = [
        (value: any) =>
          !value ||
          value.size < 100000 ||
          'logo must be less than 100kb',
      ]
    }
  }
  async validateForm() {
    if ((this.$refs.sellerForm as Vue & { validate: () => boolean }).validate()) {
      await this.processRequest();
    }
  }
  resetForm() {
    this.parentCategoryInfo = new ParentCategoryInfo();
    this.selectedImage = null;
    (this.$refs.sellerForm as Vue & { reset: () => boolean }).reset();
  }
  previewImage() {
    console.log(this.selectedImage)
    if (this.selectedImage)
      this.selectedImageUrl = URL.createObjectURL(this.selectedImage)
    else
      this.selectedImageUrl = ''
  }
  clearImages() {
    this.selectedImageUrl = ""
  }
  async processRequest() {
    try {
      if (this.edit)
        await this.updateSeller();
      else {
        await this.addSeller();

      }
    } catch (error) {

    }
  }
  async addSeller() {
    if (this.selectedImage != null) {
      try {
        this.loading = true;
        this.parentCategoryInfo.id = UtilityService.convertToKebabCase(this.parentCategoryInfo.name) as string;
        this.parentCategoryInfo.searchPhrase = this.parentCategoryInfo.name.toLowerCase();
        const fileExtension = this.getFileExtension(this.selectedImage);
        const metadata = {
          cacheControl: 'public,max-age=172800',
        };
        const resp = await storage.ref('category-logos/' + this.parentCategoryInfo.id + '-logo.' + fileExtension).put(this.selectedImage, metadata);
        const url = await resp.ref.getDownloadURL();
        this.parentCategoryInfo.logoUrl = url;
        const dateNow = new Date();
        const auditfield: AuditFields = {
          createdBy: { uid: auth.currentUser?.uid, name: auth.currentUser?.displayName, email: auth.currentUser?.email },
          createdOn: dateNow.getTime()
        }
        console.log(url);
        this.parentCategoryInfo.auditTrail = auditfield;
        const dbResp = await FirestoreService.addDocumentWithId('parentCategories', this.parentCategoryInfo.id, this.parentCategoryInfo, ParentCategoryInfo.converter);
        Vue.toasted.show('Parent Category Added Successfully', { icon: 'face', type: 'success' });

        this.resetForm();
      } catch (error) {
        console.log(error)
        Vue.toasted.show(error.message, { icon: 'cross', type: 'error' });

      } finally {
        this.loading = false;
      }
    }
  }
  getFileExtension(selectedImage: any) {
    return selectedImage.name.substring(selectedImage.name.lastIndexOf(".") + 1);
  }

  cancelEdit() {
    this.$store.commit('setCloseSeller', false)
  }
  async updateSeller() {

    try {
      this.loading = true;
      this.parentCategoryInfo.id = UtilityService.convertToKebabCase(this.parentCategoryInfo.name) as string;
      this.parentCategoryInfo.searchPhrase = this.parentCategoryInfo.name.toLowerCase();

      if (this.selectedImage != null) {
        const fileExtension = this.getFileExtension(this.selectedImage);
        const metadata = {
          cacheControl: 'public,max-age=172800',
        };
        const resp = await storage.ref('category-logos/' + this.parentCategoryInfo.id + '-logo.' + fileExtension).put(this.selectedImage, metadata);
        const url = await resp.ref.getDownloadURL();
        this.parentCategoryInfo.logoUrl = url;
        console.log(this.parentCategoryInfo.logoUrl);

      }

      const dateNow = new Date();
      const auditfield: AuditFields = {
        createdOn: this.parentCategoryInfo.auditTrail.createdOn,
        createdBy: this.parentCategoryInfo.auditTrail.createdBy,
        updatedBy: { uid: auth.currentUser?.uid, name: auth.currentUser?.displayName, email: auth.currentUser?.email },
        updatedOn: dateNow.getTime()
      }
      this.parentCategoryInfo.auditTrail = auditfield;
      const dbResp = await FirestoreService.setDocument('parentCategories', this.parentCategoryInfo.id, this.parentCategoryInfo, ParentCategoryInfo.converter);
      Vue.toasted.show('Parent Category Updated Successfully', { icon: 'face', type: 'success' });
      this.resetForm();
    } catch (error) {
      console.log(error)
      Vue.toasted.show(error.message, { icon: 'cross', type: 'error' });
    } finally {
      this.loading = false;
      this.$store.commit('setCloseSeller', false)
    }
  }




}
