import { classToPlain, plainToClass } from 'class-transformer';
import { AuditFields } from './AuditFields';

export class ParentCategoryInfo {
    id: string;
    name: string;
    description: string;
    logoUrl: string;
    auditTrail!: AuditFields;
    searchPhrase: string;

    constructor() {
        this.id = "";
        this.name = "";
        this.description = "";
        this.logoUrl = "";
        this.searchPhrase = "";

    }
    static converter = {
        toFirestore(expense: ParentCategoryInfo): firebase.firestore.DocumentData {
            return classToPlain(expense);
        },
        fromFirestore(
            snapshot: firebase.firestore.QueryDocumentSnapshot,
            options: firebase.firestore.SnapshotOptions
        ): ParentCategoryInfo {
            const data = snapshot.data(options)!;
            return plainToClass(ParentCategoryInfo, data);
        }
    };
}