import { Component, Prop, Vue } from "vue-property-decorator";

import store from '@/store';
import { AuditFields } from '@/dto/AuditFields';
import { FirestoreService } from '@/services/FirestoreService';
import Swal from 'sweetalert2';
import { UtilityService } from '@/services/UtilityService';
import { triggerThreadSafeValidation } from 'vee-validate/dist/types/components/common';
import ParentCategory from '@/components/parentCategory/ParentCategory.vue'
import { ParentCategoryInfo } from '@/dto/ParentCategoryInfo';
@Component({
  components: {
    ParentCategory
  }
})
export default class ParentCategories extends Vue {


  sellerSearchLoading = false;
  sellerList: ParentCategoryInfo[] = [];
  loading = false;
  searchDisabled = false;
  queryString = "";
  searchLoading = false;
  headers: any = [
    { text: 'Date Added', value: 'auditTrail.createdOn', },
    { text: 'Parent Category Id', value: 'id' },
    { text: 'Parent Category Name', value: 'name' },
    { text: 'Logo', value: 'logoUrl' },


    { text: 'Added By', value: 'auditTrail.createdBy.email' },
    { text: 'Updated By', value: 'auditTrail.updatedBy.email' },
    { text: 'Actions', value: 'actions', sortable: false },

  ]
  editedIndex = 0;
  editedItem: ParentCategoryInfo = new ParentCategoryInfo();
  dialog = false;


  async searchProducts() {
    console.log('query:' + this.queryString)
    if (this.queryString !== "") {
      await this.searchQueryString();
    } else {
      await this.getProducts();
    }
  }
  async getProducts() {
    try {
      this.sellerList = [];
      this.sellerSearchLoading = true;
      const snapshot = await FirestoreService.getDocuments('parentCategories', ParentCategoryInfo.converter);
      if (!snapshot.empty) {
        snapshot.forEach((doc: any) => {
          this.sellerList.push(doc.data());
        });
        Vue.toasted.show('Search complete', { icon: 'face', type: 'success' });

      } else {
        Vue.toasted.show('No results found', { icon: 'face', type: 'error' });
      }


    } catch (error) {
      console.log(error)

    }
    finally {
      this.sellerSearchLoading = false;
    }

  }
  editItem(item: ParentCategoryInfo) {
    this.editedIndex = this.sellerList.indexOf(item)
    this.editedItem = Object.assign({}, item)
    this.$store.commit('setCloseSeller', true)
  }



  async deleteItem(item: ParentCategoryInfo) {
    try {
      const res = await Swal.fire({
        title: 'Are you sure?',
        text: "All Sub Categries will also be deleted!!. You won't be able to revert this!",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes, delete it!',

      });
      if (res.isConfirmed) {
        await FirestoreService.deleteDocument(`parentCategories`, item.id);
        const deleteIndex = this.sellerList.indexOf(item)

        this.sellerList.splice(deleteIndex, 1)
        Swal.fire(
          'Deleted!',
          'Parent Category has been deleted.',
          'success'
        )
      }
    } catch (error) {
      Vue.toasted.show(error.message, { icon: 'face', type: 'error' });

    }

  }
  async searchQueryString() {
    try {
      this.searchDisabled = true;
      this.searchLoading = true;
      this.sellerList = [];
      const snapshot = await FirestoreService.getdocsStartingWithKey('parentCategories', this.queryString, 'name', ParentCategoryInfo.converter);
      if (!snapshot.empty) {
        snapshot.forEach((doc: any) => {
          this.sellerList.push(doc.data());
        });
        Vue.toasted.show('Found ' + this.sellerList.length + ' items', { icon: 'face', type: 'success' });

      } else {
        Vue.toasted.show('No results found', { icon: 'face', type: 'error' });
      }

    } catch (error) {
      Vue.toasted.show(error.message, { icon: 'face', type: 'error' });

    } finally {
      this.searchLoading = false;
      this.searchDisabled = false;
      this.queryString = ""
    };
  }
}
